import { Routes, Route, Navigate } from "react-router-dom"
import { ContactContainer, DetailsTermContainer, TermsOfUserContainer } from "../containers";
import { Typography, useTheme } from "@mui/material";

export const AppRoutes = () => {
  const theme = useTheme();

  return (
    <Routes>
      <Route path="/termos-de-uso" element={<TermsOfUserContainer />} />
      <Route 
        path="/details-introduction" 
        element={
          <DetailsTermContainer title="Introduo" body={
            <>
            <Typography fontSize={theme.spacing(2)} component="p" variant="body1">Obrigado por escolher a Pure Games.</Typography>
            <Typography fontSize={theme.spacing(2)} component="p" variant="body1">Ns nos comprometemos a proteger a privacidade dos usurios do nosso jogo.</Typography>
            <Typography fontSize={theme.spacing(2)} component="p" variant="body1">Esta Poltica de Privacidade explica como coletamos, usamos, divulgamos e salvaguardamos suas informaes quando voc joga nossos jogos. Ns nos reservamos o direito de fazer alteraes a esta Poltica de Privacidade a qualquer momento e por qualquer motivo. Iremos alert-lo sobre quaisquer alteraes, publicando a dta da ltima atualizao desta Poltica de Privacidade. Voc  encorajado a revisar periodicamente esta Poltica de Privacidade para se manter informado de atualizaes.</Typography>
            </>
          } />
        }
      />
      <Route 
        path="/details-collect-information" 
        element={
          <DetailsTermContainer title="Coleta de suas informaes" body="Em Desenvolvimento" />
        }
      />
      <Route 
        path="/details-use-information" 
        element={
          <DetailsTermContainer title="Uso das informaes" body="Em Desenvolvimento" />
        }
      />
      <Route 
        path="/details-politic-data" 
        element={
          <DetailsTermContainer title="Politica de Dados" body="Em Desenvolvimento" />
        }
      />
      
      <Route
        path="/contact"
        element={<ContactContainer />}
      />

      <Route path="*" element={<Navigate to="/termos-de-uso" />}/>


    </Routes>
  )
}