import { CardWithPhotograph } from '../components';
import introducao from '../../assets/Foto_Introducao.jpg';
import colataDados from '../../assets/Foto_Coleta_Dados.png';
import usoDosDados from '../../assets/Foto_Uso_Das_Informacoes.jpg';
import politicaDeDados from '../../assets/Foto_Politica_Anuncios.jpg';
import { BaseLayout } from '../layout'
import { Box } from '@mui/material'
import { CardWithPhotographProps } from '../interfaces';

export const TermsOfUserContainer = () => {

    const cardListData: CardWithPhotographProps[] = [
        {
            image: introducao,
            preface: 'Obrigado por escolher nossos jogos. Ns nos comprometemos a proteger a privacidade dos usurios do nosso jogo. Esta Poltica de Privacidade explica como coletamos, usamos, divulgamos e salvaguardamos suas informaes quando voc joga nossos jogos.      ',
            title: 'Introduo',
            to: 'details-introduction'
        },
        {
            image: colataDados,
            preface: 'Podemos coletar informaes sobre voc de vrias maneiras. Os dados podem incluir o seguinte: por meio de formulrios criados no jogo, por meio de informaes legais retiradas da plataforma que voc utilizar, etc...',
            title: 'Coleta de informaes',
            to: 'details-collect-information'
        },
        {
            image: usoDosDados,
            preface: 'Ter acesso s suas informaes nos permite oferecer a voc uma melhorar experincia do usurio. Usamos suas informaes para: Melhorar, personalizar e expandir o nosso jogo, dentre muitas outras.',
            title: 'Poltica de Anncios',
            to: 'details-use-information'
        },
        {
            image: politicaDeDados,
            preface: 'Usamos anncios para financiar nossos jogos e torn-los gratuitos para voc. Trabalhamos com parceiros de publicidade que coletam informaes para fornecer anncios personalizados de acordo com seus interesses.',
            title: 'Uso de suas informaes',
            to: 'details-politic-data'
        }
    ]    
    return (
        <BaseLayout title='Termos de Uso'>
        <Box
            sx={{
                width: "100%",
                height: "100%",
                display: "flex",
                justifyContent: "center"
            }}
        >
            {
                cardListData.map((item) => (
                    <CardWithPhotograph to={item.to} image={item.image} title={item.title} preface={item.preface} />
                ))
            }
        </Box>
        </BaseLayout>
    )
}


/*
<Typography># Poltica de Privacidade para [Nome do Seu Jogo]

## 1. Introduo

Obrigado por escolher [Nome do Seu Jogo] ("ns", "nos", "nosso"). Ns nos comprometemos a proteger a privacidade dos usurios do nosso jogo. Esta Poltica de Privacidade explica como coletamos, usamos, divulgamos e salvaguardamos suas informaes quando voc joga [Nome do Seu Jogo].

Ns nos reservamos o direito de fazer alteraes a esta Poltica de Privacidade a qualquer momento e por qualquer motivo. Iremos alert-lo sobre quaisquer alteraes, publicando a "data da ltima atualizao" desta Poltica de Privacidade. Voc  encorajado a revisar periodicamente esta Poltica de Privacidade para se manter informado de atualizaes.

## 2. Coleta de suas informaes

Podemos coletar informaes sobre voc de vrias maneiras. Os dados podem incluir o seguinte:

### 2.1 Dados de Uso

As informaes coletadas automaticamente quando voc joga o jogo podem incluir seu dispositivo mvel e detalhes de uso, incluindo informaes sobre suas visitas ao jogo, incluindo o contedo visualizado, os recursos que voc acessou, as aes no jogo, a data e hora de sua visita, o tempo gasto no jogo e outras informaes de estatsticas.

## 3. Uso de suas informaes

Ter acesso s suas informaes nos permite oferecer a voc o [Nome do Seu Jogo] e melhorar a experincia do usurio. Usamos suas informaes para:

- Melhorar, personalizar e expandir o nosso jogo
- Compreender e analisar a utilizao do nosso jogo
- Desenvolver novos produtos, servios, caractersticas e funcionalidades

## 4. Poltica de Anncios

Usamos anncios para financiar o [Nome do Seu Jogo] e torn-lo gratuito para voc. Trabalhamos com parceiros de publicidade que coletam informaes para fornecer anncios personalizados de acordo com seus interesses.

## 5. Contatos

Se voc tiver alguma dvida ou comentrio sobre esta Poltica de Privacidade, entre em contato conosco em: [Seu endereo de e-mail]
</Typography>
*/